import React, { FC } from 'react';
import { useForm } from 'react-hook-form';
import { RouteComponentProps } from 'react-router';
import { EditForm, EditViewProps, getSelectedItem, InputGroupInline, WindowComponent } from 'summer';
import { CLASS_NAME, NcmGenero } from '../../../../models/dtos/mlf/ncmGenero';

interface NcmGeneroEditViewProps extends EditViewProps<NcmGenero> {}

const NcmGeneroEditView: FC<NcmGeneroEditViewProps & RouteComponentProps> = props => {
  const selectedItem = getSelectedItem(props);
  const formProps = useForm<NcmGenero>({ defaultValues: selectedItem, mode: 'onBlur' });

  return (
    <WindowComponent title="Manutenção de Gênero NCM">
      <EditForm formProps={formProps} editViewProps={props} dtoClassName={CLASS_NAME}>
        <InputGroupInline
          id="codigo"
          label="Código"
          formProps={{ ...formProps, validation: { required: true, maxLength: 2 } }}
          isPrependLabel={true}
          placeholder="Informe o código"
        />

        <InputGroupInline
          id="descricao"
          isPrependLabel={true}
          formProps={{ ...formProps, validation: { required: true, maxLength: 300 } }}
          label="Descrição"
          placeholder="Informe a descrição"
        />
      </EditForm>
    </WindowComponent>
  );
};

export default NcmGeneroEditView;
